<!-- 个人生活习惯 -->
<template>
	<div id="personal-hab">
		<!-- <div class="title">
			<router-link to ="/"><span><img :src="img1"></span></router-link><label>健康档案</label>
		</div> -->
		<div class="head">
			<span><img :src="img2"></span><label>个人生活习惯</label>
		</div>
		<div class="content">
			<ul>
				<li v-for = "(val,key) in contents">
				<span class="key">{{key}}</span>
				<span class="val">{{val}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'personal-hab',
		data(){
			return{
				userId:"",
				img2:require("../assets/shenghuo.png"),
				contents:{
					"吸烟状况":"",
					"吸烟时长":"",
					"饮酒频率":"",
					"饮食习惯名称":"",
					"结果描述":""
				},
				lifeHabbit:{}
			}
		},
		mounted(){
				var head = document.getElementsByClassName("head");
				var content = document.getElementsByClassName("content");
				head[0].style.backgroundColor = "#fff";
				content[0].style.backgroundColor = "#fff";
				document.body.style.backgroundColor = "#f0f0f0";
				var val = document.getElementsByClassName("val");
				
				this.lifeHabbit = this.$route.query.lifeHabbit;
				this.contents.吸烟状况 = this.lifeHabbit.smokeCondition;
				this.contents.吸烟时长 = this.lifeHabbit.smokeDuration;
				this.contents.饮酒频率 = this.lifeHabbit.drinkFrequency;
				this.contents.饮食习惯名称 = this.lifeHabbit.eatHabitName;
				this.contents.结果描述 = this.lifeHabbit.resultDescribe;
				}
	}
</script>

<style scoaped>
#personal-hab .head,.content{
	width: 100%;
}
#personal-hab .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#personal-hab .content{
	margin-top: 0.1rem;
}
#personal-hab .head span,#personal-hab .head label{
	line-height: 0.35rem;
}	
#personal-hab .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#personal-hab .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}

ul{
	list-style-type: none;
	margin:0rem;
	padding: 0rem;
}
.content ul li{
	width: 100%;
	height: 0.55rem;
	line-height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
.content ul li span.key{
	float: left;
	margin-left: 0.19rem;

}
.content ul li span.val{
	float: right;
	margin-right: 0.1rem;
}
</style>


