<!-- 个人生活环境 -->
<template>
	<div id="personal-life">
		<!-- <div class="title">
			<router-link to ="/"><span><img :src="img1"></span></router-link><label>健康档案</label>
		</div> -->
		<div class="head">
			<span><img :src="img2"></span><label>生活环境</label>
		</div>
		<div class="content">
			<ul>
				<li v-for = "(val,key) in contents">
				<span class="key">{{key}}</span>
				<span class="val">{{val}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'personal-life',
		data(){
			return{
				userId:"",
				img2:require("../assets/huanjing.png"),
				contents:{
					"家庭厕所类别":"其他",
					"家庭厨房排风设施类别":"其他",
					"家庭禽畜栏类别":"其他",
					"家庭燃料类型":"其他"
				},
			}
		},
		mounted(){
				var head = document.getElementsByClassName("head");
				var content = document.getElementsByClassName("content");
				head[0].style.backgroundColor = "#fff";
				content[0].style.backgroundColor = "#fff";
				document.body.style.backgroundColor = "#f0f0f0";
				var val = document.getElementsByClassName("val");
				val[1].style.marginLeft = 0.41 + 'rem';
				val[2].style.marginLeft = 0.86 + 'rem';
				}
		
	}
</script>

<style scoaped>
#personal-life .head,.content{
	width: 100%;
}
#personal-life .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#personal-life .content{
	margin-top: 0.1rem;
}
#personal-life .head span,#personal-life .head label{
	line-height: 0.35rem;
}	
#personal-life .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#personal-life .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}

ul{
	list-style-type: none;
	margin:0rem;
	padding: 0rem;
}
.content ul li{
	width: 100%;
	height: 0.55rem;
	line-height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
.content ul li span.key{
	float: left;
	margin-left: 0.19rem;

}
.content ul li span.val{
	float: right;
	margin-right: 0.1rem;
}
</style>


