<!-- 家族史 -->
<template>
	<div id="family-his">
		<!-- <div class="title">
			<router-link to ="/"><span><img :src="img1"></span></router-link><label>健康档案</label>
		</div> -->
		<div class="head">
			<span><img :src="img2"></span><label>家族史</label>
		</div>
		<div class="content">
			<ul>
				<li v-for = "(val,key) in contents">
				<span class="key">{{key}}</span>
				<span class="val">{{val}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'family-his',
		data(){
			return{
				userId:"",
				img2:require("../assets/jiazu.png"),
				contents:{
					"父亲":"",
					"母亲":"",
					"兄弟姐妹":"",
					"家族遗传病史":"",
					"残疾情况":""
				},
				familyHistory:{}
			}
		},
		mounted(){

				var head = document.getElementsByClassName("head");
				var content = document.getElementsByClassName("content");
				head[0].style.backgroundColor = "#fff";
				content[0].style.backgroundColor = "#fff";
				document.body.style.backgroundColor = "#f0f0f0";
				var val = document.getElementsByClassName("val");
				val[2].style.marginLeft = 0.66 + 'rem';
				val[3].style.marginLeft = 0.37 + 'rem';

				this.familyHistory = this.$route.query.familyHistory;
				this.contents.父亲 = this.familyHistory.father;
				this.contents.母亲 = this.familyHistory.mother;
				this.contents.兄弟姐妹 = this.familyHistory.broAndSis;
				this.contents.家族遗传病史 = this.familyHistory.familyInheritance;
				this.contents.残疾情况 = this.familyHistory.disableCondition;

				}
	}
</script>

<style scoaped>

#family-his .head,.content{
	width: 100%;
}
#family-his .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#family-his .content{
	margin-top: 0.1rem;
}
#family-his .head span,#family-his .head label{
	line-height: 0.35rem;
}	
#family-his .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#family-his .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}
ul{
	list-style-type: none;
	margin:0rem;
	padding: 0rem;
}
.content ul li{
	width: 100%;
	height: 0.55rem;
	line-height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
.content ul li span.key{
	float: left;
	margin-left: 0.19rem;

}
.content ul li span.val{
	float: right;
	margin-right: 0.1rem;
}
</style>


