<!-- 个人健康档案 -->
<template>
	<div id="personal-doc">
		<div class="head">
			<span><img :src="img2"></span><label>个人健康档案</label>
		</div>
		<div class="content">
			<ul>
				<li v-for = "(val,key) in contents">
				<span class="key">{{key}}</span>
				<span class="val">{{val}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'personal-doc',
		data(){
			return{
				userId:"",
				img2:require("../assets/jiankang.png"),
				contents:{
					"档案状态":"",
					"档案编号":"",
					"建档机构":"",
					"管理机构":"",
					"责任医生":"",
					"糖尿病标志":"",
					"高血压标志":"",
					"高血脂标志":"",
					"高血脂高危等级":""
				},
				basicInfo:{}
				
			}	
		},
		methods:{
			 
				},
		mounted(){
						var head = document.getElementsByClassName("head");
						var content = document.getElementsByClassName("content");
						head[0].style.backgroundColor = "#fff";
						content[0].style.backgroundColor = "#fff";
						document.body.style.backgroundColor = "#f0f0f0";
						var key = document.getElementsByClassName("key");
						key[0].style.color = "rgb(18,183,137)" ;
						key[1].style.color = "rgb(18,183,137)" ;
						key[3].style.color = "rgb(18,183,137)" ;
						key[4].style.color = "rgb(18,183,137)" ;
						var val = document.getElementsByClassName("val");
						val[0].style.color = "rgb(255,102,102)" ;
						val[1].style.color = "rgb(255,102,102)" ;
						val[4].style.color = "rgb(255,102,102)" ;


						this.basicInfo = this.$route.query.basicInfo;
						this.contents.档案状态 = this.basicInfo.docStatus;
						this.contents.档案编号 = this.basicInfo.docNum;
						this.contents.建档机构 = this.basicInfo.docOrg;
						this.contents.管理机构 = this.basicInfo.docManage;
						this.contents.责任医生 = this.basicInfo.doctorName;
						this.contents.糖尿病标志 = this.basicInfo.glycuresisTag;
						this.contents.高血压标志 = this.basicInfo.highbloodTag;
						this.contents.高血脂标志 = this.basicInfo.highfatTag;
						this.contents.高血脂高危等级 = this.basicInfo.highfatLever;
						// console.log(this.basicInfo);

				}

	}
</script>

<style scoaped>
#personal-doc .head{
	width: 100%;
}
#personal-doc .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#personal-doc .content{
	margin-top: 0.1rem;
}
#personal-doc .head span,#personal-doc .head label{
	line-height: 0.35rem;
}	
#personal-doc .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#personal-doc .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}
ul{
	list-style-type: none;
	margin:0rem;
	padding: 0rem;
}
.content ul li{
	width: 100%;
	height: 0.55rem;
	line-height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
.content ul li span.key{
	float: left;
	margin-left: 0.19rem;

}
.content ul li span.val{
	float: right;
	margin-right: 0.1rem;
}
	

</style>


