<!-- 个人既往史 -->
<template>
	<div id="personal-his">
		<!-- <div class="title">
			<router-link to ="/"><span><img :src="img1"></span></router-link><label>健康档案</label>
		</div> -->
		<div class="head">
			<span><img :src="img2"></span><label>个人既往史</label>
		</div>
		<div class="content">
			<ul>
				<li v-for = "(val,key) in contents">
				<span class="key">{{key}}</span>
				<span class="val">{{val}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'personal-his',
		data(){
			return{
				userId:"",
				img2:require("../assets/jiwangshi.png"),
				contents:{
					"疾病史":"",
					"药物过敏史":"",
					"手术史":"",
					"结核病史":"",
					"外伤":"",
					"过敏药物":"",
					"输血情况":""
					},
				personalHis:{}
			}
		},
		mounted(){
					var head = document.getElementsByClassName("head");
					var content = document.getElementsByClassName("content");
					head[0].style.backgroundColor = "#fff";
					content[0].style.backgroundColor = "#fff";
					document.body.style.backgroundColor = "#f0f0f0";
					var val = document.getElementsByClassName("val");
					val[1].style.marginLeft = 0.51 + 'rem';
					val[2].style.marginLeft = 0.66 + 'rem';
					val[3].style.marginLeft = 0.66 + 'rem';

					this.personalHis = this.$route.query.personalHistory;
					this.contents.疾病史 = this.personalHis.diseaseHis;
					this.contents.药物过敏史 = this.personalHis.medicineHis;
					this.contents.手术史 = this.personalHis.operationHis;
					this.contents.结核病史 = this.personalHis.tuberculosisHis;
					this.contents.外伤 = this.personalHis.traumato;
					this.contents.过敏药物 = this.personalHis.acrosthsiaMedcine;
					this.contents.输血情况 = this.personalHis.transfusion;
				}
		
	}
</script>

<style scoaped>
#personal-his .head,.content{
	width: 100%;
}
#personal-his .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#personal-his .content{
	margin-top: 0.1rem;
}
#personal-his .head span,#personal-his .head label{
	line-height: 0.35rem;
}	
#personal-his .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#personal-his .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}
ul{
	list-style-type: none;
	margin:0rem;
	padding: 0rem;
}
.content ul li{
	width: 100%;
	height: 0.55rem;
	line-height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
.content ul li span.key{
	float: left;
	margin-left: 0.19rem;

}
.content ul li span.val{
	float: right;
	margin-right: 0.1rem;
}

</style>


