<!-- 主界面入口 -->
<template>
	<div id="total-show">
		<div id="bac"></div>
	    <div id="card">
			<img :src="img.bg" class="bg">
			<div>
				<span class="user">{{card.name}}
				</span>	
				<span class="info">
					<img :src="tag===1?img.man:img.woman">
					<span>{{card.merried}}</span>
				</span>
			</div>
			<div class="detail">
				<ul>
					<li><img :src="img.ethnic"><span class="key">民族</span><span class="val">{{card.nation}}</span></li>
					<li><img :src="img.birth"><span class="key">出生日期</span><span class="val-1">{{card.birthDay}}</span></li>
					<li><img :src="img.blood"><span class="key">血型</span><span class="val">{{card.bloodType}}</span></li>
				</ul>
			</div>
		</div>
		<div style="width: 100%;overflow-x: hidden;position: relative;">
			<router-link :to = "{path:'/health',query:{basicInfo}}" @click.native="personalDoc()">
					<div class="personal-doc">
						<ul>
							<li>
								<img :src="img.health" class="icon1">
								<span class="line"></span>
								<span class="word">个人健康档案</span>
								<img :src="img.right" class="icon2">
							</li>
						</ul>
					</div>
			</router-link>
			<router-link :to ="{path:'/history',query:{personalHistory}}"  @click.native="personalHis()">
					<div class="personal-his">
						<ul>
							<li>
								<img :src="img.jiwangshi" class="icon1">
								<span class="line"></span>
								<span class="word">个人既往史</span>
								<img :src="img.right" class="icon2">
							</li>
						</ul>
					</div>
			</router-link>
			<router-link :to ="{path:'/family',query:{familyHistory}}"  @click.native="familyHis()">
					<div class="family-his">
						<ul>
							<li>
								<img :src="img.jiazu" class="icon1">	
								<span class="line"></span>
								<span class="word">家族史</span>
								<img :src="img.right" class="icon2">
							</li>
						</ul>
					</div>
			</router-link>
			<router-link :to ="{path:'/habbit',query:{lifeHabbit}}"  @click.native="personalHab()">
					<div class="personal-life">
						<ul>
							<li>
								<img :src="img.life" class="icon1">
								<span class="line"></span>
								<span class="word">个人生活习惯</span>
								<img :src="img.right" class="icon2">
							</li>
						</ul>
					</div>
			</router-link>
			<router-link to ="/life"  @click.native="personalHab()">
					<div class="personal-hab">
						<ul>
							<li>
								<img :src="img.env" class="icon1">
								<span class="line"></span>
								<span class="word">生活环境</span>
								<img :src="img.right" class="icon2">
							</li>
						</ul>
					</div>
			</router-link>
			<div class="no"></div>
		</div>
	</div>
</template>
<script>

	export default{
		name:'total-show',
		data(){
			return{
				userId:"",
				img:{
					bg:require("../assets/shuiyin.png"),
					man:require("../assets/man.png"),
					woman:require("../assets/woman.png"),
					ethnic:require("../assets/ethnic.png"),
					birth:require("../assets/birth.png"),
					blood:require("../assets/blood.png"),
					//card:require("../assets/card.png"),
					health:require("../assets/jiankang.png"),
					right:require("../assets/rightdir.png"),
					jiwangshi:require("../assets/jiwangshi.png"),
					jiazu:require("../assets/jiazu.png"),
					life:require("../assets/shenghuo.png"),
					env:require("../assets/huanjing.png"),
				},
				card:{},
				basicInfo:{},
				personalHistory:{},
				familyHistory:{},
				lifeHabbit:{},
				tag:1,
				tag1:0
			}
		},
		  methods:{
		 	personalDoc:function(){
		 		MobclickAgent.onEvent("selfHealthRecord_click");
		  	},
		  	personalHis:function(){
		  		MobclickAgent.onEvent("selfHistory_click");
		  	},
		 	familyHis:function(){
		  		MobclickAgent.onEvent("familyHistory_click");
		  	},
		  	personalHab:function(){
		  		MobclickAgent.onEvent("selfHabit_click");
		  	},
			 personalLife:function(){
		 		MobclickAgent.onEvent("lifeStation_click");
		  	}
		  },
		mounted(){ 
					var _this = this;
					document.body.style.backgroundColor = "#fff";
					var info = document.getElementsByClassName("info");
					if (_this.tag1) {
 						info[0].style.width = 0.2 + 'rem';
 					}

				},
		created(){
				var _this = this;
				// let userInfo = JSON.parse(window.ele_health_card.getUserInfo());
				//   alert(userInfo.userId);
				// this.userId = userInfo.userId;

			//this.userId = "ec4a9301-1485-4c84-82eb-f1c642391929";

			//bloodtType,born,gender:sex,marStatus,medCardCode,medNumber,name,nation
			 /*this.$http.get('http://183.136.187.224:18301/hangzhouApp/hghdc/getPerBasicInfo',{
					params:{
						userId:_this.userId
						 //userId:"ec4a9301-1485-4c84-82eb-f1c642391929"
					}
			  }).then(function(data){
 					// console.log(data);
 					//data.data.name = null;
 					if (data.data.name === null){
 						var	nodoc = document.getElementById("total-show");
 						nodoc.innerHTML = "<img src='./static/none.png' id='none'/>";

 					}
 					else{
 					_this.card.user = data.data.name;
 					_this.card.sex = data.data.gender;
 					_this.card.ethnic = data.data.nation;
 					_this.card.birth_day = data.data.born;
 					_this.card.blood_type = data.data.bloodType;
 					_this.card.marry_info = data.data.marStatus;

 					if (_this.card.marry_info != "未婚" || _this.card.marry_info != "已婚"){
						_this.tag1 = 1;
						return _this.tag1;
					}
 					_this.card.marry_info = data.data.marStatus;
 					if (_this.card.sex === "女性"){
						_this.tag = 0;
						return _this.tag;
					}
				}
 					
			 })*/
			 this.$http.get('https://nei.netease.com/api/apimock/87413c3ba03842647c8213328f5b997e/getHealthDocument',{
			 	params:{
			 		userId:'152461551661561'
			 	}
			 }).then((res) => {
			 	//console.log(res.data);
			 	this.card = res.data;
			 	this.basicInfo = this.card.basicInfo;
			 	this.personalHistory = this.card.personalHis;
			 	this.familyHistory = this.card.familyHis;
			 	this.lifeHabbit = this.card.lifeHabbit;
			 	if (this.card.name === null){
 						var	nodoc = document.getElementById("total-show");
 						nodoc.innerHTML = "<img src='./static/none.png' id='none'/>";

 					}
 				if (this.card.merried != "未婚" || this.card.merried != "已婚"){
						this.tag1 = 1;
						return this.tag1;
					}
 				if (this.card.sex === "女"){
						this.tag = 0;
						return this.tag;
					}

			 }).catch(function(err){
			 	console.log(err);
			 })
		}
	}	
</script>

<style scoaped>
#total-show{
	width: 100%;
	overflow-x: hidden;
}
#bac{
	width: 100%;
	height: 0.8rem;
	border-radius: 0 0 19.6% 19.6%;
	background:-moz-linear-gradient(rgb(6,181,131),rgb(62,191,160));
	background:-webkit-linear-gradient(rgb(6,181,131),rgb(62,191,160));
	background:linear-gradient(rgb(6,181,131),rgb(62,191,160));
}
#card{
	width: 90%;
	height: 2rem;
	margin-top: -0.58rem;
	margin-left: 5.2%;
	background:rgb(255,255,255);
	border-radius: 0.1rem;
	-moz-box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	-webkit-box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	z-index: 2;

}
.bg{
	width:48%;
	height: 1.57rem;
	margin-top: 0.22rem;
	margin-left: 47%;
	z-index:4;

}
#card div{
	width: 1.28rem;
	height: 0.19rem;
	z-index:6; 
	margin-top:-1.75rem;
	margin-left: 0.25rem;
}
.user{
	font-size: 0.2rem;
	font-weight: bold;
	float: left;
	margin-top: -0.05rem;
}
.info{
	width: 0.5rem;
	height: 0.15rem;
	line-height: 0.16rem;
	background-color: rgb(127,160,248);
	float: left;
	margin-top: 0.03rem;
	margin-left: 0.12rem;
	border-radius: 0.075rem;
	font-size: 0.1rem;
	color: #fff;
}
.info img{
	width: 0.1rem;
	height: 0.1rem;
	float: left;
	margin-top: 0.025rem;
	margin-left: 0.055rem;
}
.info span{
	margin-left: 0.05rem;
}
#card .detail{
	width: 2.158rem;
	height: 0.97rem;
	margin-top: 0.26rem;
	margin-left: 0.25rem;
}
ul{
	list-style-type: none;
	margin: 0rem;
	padding: 0rem;
}
a{

	text-decoration: none;
}
.detail ul li{
	width: 2.158rem;
	height: 0.18rem;
	line-height: 0.16rem;
	font-size: 0.14rem;
	margin-top: 0.16rem;
}
.detail img{
	width: 0.15rem;
	height: 0.13rem;
}
.detail .key{

	margin-left: 0.19rem;
}
.detail .val{
	margin-left: 0.645rem;
	font-size: 0.15rem;
	color: rgb(137,137,137);

}
.detail .val-1{
	margin-left: 0.35rem;
	font-size: 0.15rem;
	color: rgb(137,137,137);
}
.personal-doc,.personal-his,.family-his,.personal-life,.personal-hab{
	width: 90%;
	height: 0.65rem;
	margin-top: 0.15rem;
	margin-left: 5%;
	border-radius: 0.1rem;
	-moz-box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	-webkit-box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	background-color: rgb(255,255,255);
}
.no{
	width: 100%;
	height: 0.1rem;
	
}
.personal-doc ul li,.personal-his ul li,.family-his ul li,.personal-life ul li,.personal-hab ul li{
	width: 3.45rem;
	height: 0.65rem;
	line-height: 0.65rem;
	font-size: 0.17rem;
	color: rgb(109,109,109);
}
.personal-doc .icon1,.personal-his .icon1,.family-his .icon1,.personal-life .icon1,.personal-hab .icon1{
	width: 13%;
	height: 0.45rem;
	margin-top: 0.1rem;
	margin-left: 6%;
	float: left;
} 
.personal-doc .line,.personal-his .line,.family-his .line,.personal-life .line,.personal-hab .line{
	display: block;
	width: 0.5%;
	height: 0.35rem;
	margin-left: 13%;
	margin-top: 0.15rem;
	background:rgb(238,238,238);
	float: left;
}
.personal-doc .word,.personal-his .word,.family-his .word,.personal-life .word,.personal-hab .word{

	margin-left: 9.9%;
}
.personal-doc .icon2,.personal-his .icon2,.family-his .icon2,.personal-life .icon2,.personal-hab .icon2{
	width: 2.5%;
	height: 0.14rem;
}
.personal-doc .icon2,.personal-life .icon2{
	margin-left: 0.6rem;
}
.personal-his .icon2{
    margin-left: 0.77rem;
}
.family-his .icon2{
	margin-left: 1.12rem;
}
.personal-hab .icon2{
	margin-left: 0.94rem;
}
#none{
	width: 66.67%;
	height:2.5rem;
	display: block;
	margin: 50% auto;
}

</style>



